import React from 'react'
import { v4 as uuidv4 } from 'uuid'
import TodoList from './TodoList'
import Header from './Header'
import InputTodo from './InputTodo'
class TodoContainer extends React.Component
{
   
    state = {
        todos: []
    };
    async componentDidMount(){
        try{
            const res = await fetch('https://mostafa0100.pythonanywhere.com/api/ListTodo/');
            const todos = await res.json();
            this.setState({
                todos
            });
        }
        catch(e){
            console.log(e)
        }
    }



    handleChange = (id) =>{
                            this.setState({
                                todos:this.state.todos.map(todo =>{
                                    if(todo.id === id){
                                        todo.completed = !todo.completed;
                                        fetch('https://mostafa0100.pythonanywhere.com/api/ListTodo/'+id+'/',{
                                            method:'PUT',
                                            headers:{
                                                'Content-Type':'application/json',
                                            },
                                            body:JSON.stringify(todo),
                                        }).then(response=>response.json());
                                    }
                                    return todo
                                })
                            });
                          };

    delTodo = (id) => {
        ////
        this.state.todos.map(todo =>{
            if(todo.id === id)
            {
                if(todo.completed ==true)
                {
                    const url="https://mostafa0100.pythonanywhere.com/api/ListTodo/"+id+"/"
                fetch(url,{
                    method:'DELETE',
                    headers:{
                        'Content-Type':'application/json',
                    },
                    }).then(()=>{
                        this.setState({todos:this.state.todos.filter(todo => todo.id !== id)})
                    });
                }
                else
                {
                    alert("This Task Not Completed ???")
                }
                
            }
        });
       
    };

    addTodoItem = title =>{
        const newTodo = {
            id:Math.floor((Math.random()*100)),
            title:title,
            completed:false
        };

        fetch('https://mostafa0100.pythonanywhere.com/api/ListTodo/',{
            method:'POST',
            headers:{
                'Content-Type':'application/json',
            },
            body:JSON.stringify(newTodo),
        }).then(response=>response.json())
        .then(title=>{
            this.setState({
                todos: [...this.state.todos,newTodo]
            });
        });
        
        
    };
    render()
    {
       
        return (
            <div className="container">
                <Header/>
                <InputTodo addTodoProps={this.addTodoItem}/>
                <TodoList
                     todos={this.state.todos}
                     handleChangeProps={this.handleChange} 
                     handleDelTodoProps={this.delTodo}
                />
            </div>
        );

          
    }
}

export default TodoContainer
